import { styled } from "@rp/react/theme"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Grid } from "@rp/react/web/mui/core"
import {
  RpJsonScrollWrapper,
  RpJsonSpotlightSentence,
  RpJsonTitleDisplay,
} from "@rp/react/web/rpjson"

const RpJsonFixedContent = styled(Grid, {
  shouldForwardProp: prop => prop !== "isMobile",
})<{ isMobile: boolean }>(({ theme, isMobile }) => ({
  paddingRight: isMobile ? 0 : theme.spacing(3),
}))

type RpJsonScrollWrapperStyledProps = {
  isPane?: boolean
}

const RpJsonScrollWrapperStyled = styled(RpJsonScrollWrapper, {
  shouldForwardProp: propName => propName !== "isPane",
})<RpJsonScrollWrapperStyledProps>(({ theme, isPane = false }) => ({
  "&": {
    paddingBottom: isPane ? 0 : theme.spacing(2),
    backgroundColor: isPane ? theme.palette.elevation.dp01 : "inherit",
    ...theme.onHoverScrollbarStyles,
    ":hover": {
      ...theme.scrollbarStyles,
    },
  },
}))

const ContentStyled = styled("div", { name: "ContentStyled" })(() => ({
  "& hr": {
    border: "none",
  },
}))

const DocumentMetadataContainer = styled("div")(({ theme }) => ({
  color: theme.palette.grey[300],
  fontSize: 11,
  letterSpacing: 1,
  display: "flex",
  overflow: "hidden",
  justifyContent: "left",
  alignItems: "center",
  gap: theme.spacing(1),
  margin: `${theme.spacing(2)} 0`,
  paddingRight: theme.spacing(2),
  "& span": {
    fontSize: 11,

    "& svg": {
      marginRight: theme.spacing(0.5),
    },
  },

  "& div": {
    display: "flex",
    alignItems: "center",
    "& svg": {
      height: 24,
    },
  },
}))

const DocumentTextContainer = styled("div")(({ theme }) => {
  const { isMobile } = useDeviceInfo()

  const contentBlockDefaults = {
    fontWeight: 400,
    lineHeight: "30px",
    margin: 0,
    marginTop: theme.spacing(2),
  }

  return {
    overflow: "hidden",
    width: "100%",
    display: "flex",
    flexDirection: "column",
    fontSize: isMobile ? 20 : 18,
    lineHeight: isMobile ? "32px" : "",
    paddingBottom: theme.spacing(2),
    minHeight: "100vh",

    // Content blocks
    // HEADING
    "& h3": {
      ...contentBlockDefaults,
      color: theme.palette.info.light,
    },

    // Add spacing on top of HEADINGS
    "& * + h3": {
      marginTop: theme.spacing(4),
    },

    // PARAGRAPH
    "& p": {
      ...contentBlockDefaults,
    },

    // But remove margin from paragraphs inside list items and tables
    "& li>p, & td>p": {
      marginTop: 0,
    },

    // TABLE
    "& table": {
      borderCollapse: "collapse",
      marginTop: theme.spacing(2),
    },
    "& td": {
      color: "white",
      background: theme.palette.grey[700],
      boxShadow: "inset -1px -1px 0px #515151",
      padding: theme.spacing(2),
      minWidth: 150,
    },

    // FOOTER
    "& h5": {
      ...contentBlockDefaults,
      fontSize: theme.typography.caption.fontSize,
    },

    // LISTS
    "& ul, & ol": {
      paddingInlineStart: theme.spacing(3),
    },
  }
})

const RpJsonSpotlightSentenceStyled = styled(RpJsonSpotlightSentence, {
  name: "RpJsonSpotlightSentence",
})<{ isHeadline: boolean }>(({ theme, isHeadline }) => {
  const { isMobile } = useDeviceInfo()

  if (isHeadline) {
    return {
      "& a": {
        textDecoration: "none",
        color: "inherit",
        cursor: "auto",
      },
    }
  }

  return {
    "& a": {
      color: theme.palette.text.primary,
      padding: isMobile ? "4px 0px" : "5px 0px",
      cursor: "pointer",
      textDecoration: "none",
      backgroundColor: "#0E3141",

      ":hover": {
        backgroundColor: theme.palette.primary.dark,
      },
    },
  }
})

const RpJsonTitleDisplayStyled = styled(RpJsonTitleDisplay)(({ theme }) => ({
  "& p": {
    fontSize: theme.typography.h5.fontSize,
    lineHeight: "32px",
    fontWeight: 700,
    margin: 0,
    display: "inline",
  },
}))

export {
  RpJsonFixedContent,
  RpJsonScrollWrapperStyled,
  RpJsonTitleDisplayStyled,
  DocumentMetadataContainer,
  DocumentTextContainer,
  RpJsonSpotlightSentenceStyled,
  ContentStyled,
}
